.user {
    text-align: center;
    font-family: sans-serif;
    font-size: 30px;
    color: #323941;
    padding-top: 20px;
}

.user-main {
    border-radius: 50%;
    width: 250px;
    height: 250px;
    display: inline-block;
    overflow: hidden;
    box-shadow: 0 5px 15px -5px #00000070;
}

.user-main img {
    widows: 100%;
    height: 100%;
}

.user h1 {
    margin: 0;
}
