body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  background-color: #fafafa;
}


.sns {
    position: absolute;
    width: 100%;
    text-align: center;
}

.btn {
    display: inline-block;
    width: 90px;
    height: 90px;
    background: #f1f1f1;
    background: white;
    margin: 10px;
    border-radius: 30%;
    box-shadow: 0 5px 15px -5px #00000070;
    color: #323941;
    overflow: hidden;
    position: relative;
}

.btn i{
    line-height: 90px;
    font-size: 26px;
    transition: 0.2s linear;
}

.btn:hover i{
    -webkit-transform: scale(1.3);
            transform: scale(1.3);
    color: #f1f1f1;
}

.btn::before {
    content: "";
    position: absolute;
    width: 120%;
    height: 120%;
    background: #323941;
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    left: -110%;
    top: 90%;
}

.btn:hover::before {
    -webkit-animation: aaa 0.7s 1;
            animation: aaa 0.7s 1;
    top: -10%;
    left: -10%;
}

@-webkit-keyframes aaa {
    0%{
        left: -110%;
        top: 90%;
    }50%{       
        left: 10%;
        top: -30%;
    }100%{
        top: -10%;
        left: -10%;
    }
}

@keyframes aaa {
    0%{
        left: -110%;
        top: 90%;
    }50%{       
        left: 10%;
        top: -30%;
    }100%{
        top: -10%;
        left: -10%;
    }
}
.user {
    text-align: center;
    font-family: sans-serif;
    font-size: 30px;
    color: #323941;
    padding-top: 20px;
}

.user-main {
    border-radius: 50%;
    width: 250px;
    height: 250px;
    display: inline-block;
    overflow: hidden;
    box-shadow: 0 5px 15px -5px #00000070;
}

.user-main img {
    widows: 100%;
    height: 100%;
}

.user h1 {
    margin: 0;
}

